// Simple authentication service using localStorage
// In production, this should use JWT tokens and a real backend authentication system
class AuthService {
    constructor() {
        this.STORAGE_KEY = 'logistic_admin_auth';
        // Default credentials (in production, this should be in backend)
        this.DEFAULT_CREDENTIALS = {
            username: 'admin',
            password: 'admin123', // Change this to a secure password
        };
    }
    login(username, password) {
        // In production, this should make an API call to backend
        if (username === this.DEFAULT_CREDENTIALS.username &&
            password === this.DEFAULT_CREDENTIALS.password) {
            const user = {
                username,
                role: 'admin',
            };
            localStorage.setItem(this.STORAGE_KEY, JSON.stringify(user));
            return true;
        }
        return false;
    }
    logout() {
        localStorage.removeItem(this.STORAGE_KEY);
    }
    isAuthenticated() {
        const userStr = localStorage.getItem(this.STORAGE_KEY);
        if (!userStr)
            return false;
        try {
            const user = JSON.parse(userStr);
            return !!user && user.role === 'admin';
        }
        catch {
            return false;
        }
    }
    getCurrentUser() {
        const userStr = localStorage.getItem(this.STORAGE_KEY);
        if (!userStr)
            return null;
        try {
            return JSON.parse(userStr);
        }
        catch {
            return null;
        }
    }
}
export const authService = new AuthService();
export default authService;
